<template>
	<view class="page">
		<template v-if="type == 2">
			<template v-if="trialtype == 0">
				<view class="video-wrap">
					<video show-mute-btn="true" page-gesture="true" class="video-wrap" :src="url" :poster="thumb" controls="true"
					 autoplay="true"></video>
				</view>
			</template>
			<template v-if="trialtype == 2">
				<view class="buyBG">
					<view @click="fufei" class="buytitle">购买后可学习完成内容</view>
				</view>
			</template>
		</template>
		<template v-else-if="type == 3">
			<template v-if="trialtype == 0">
				<view class="video-wrap3">
					<image :src="thumb" class="video-wrap">
					</image>
					<imt-audio :autoplay="trialtype == 0 ? true:false" class="imtaudio" :src="url"></imt-audio>
				</view>
			</template>
			<template v-if="trialtype == 2">
				<view class="buyBG">
					<view @click="fufei" class="buytitle">购买后可学习完成内容</view>
				</view>
			</template>
		</template>
		<template v-else></template>
		<view class="cdetail-all-wrap">
			<view class="content-title">{{name}}</view>
			<view class="content-des">{{des}}</view>
			<view class="content-time">{{adddtime}}</view>
			<view class="line"></view>
			<rich-text class="content-rich" :nodes="content"></rich-text>
		</view>
		<view v-if="showpayview == true" class="payview">
			<view class="cancle1">
				<view class="paytitle">支付方式</view>
				<view class="cancle" @click="cancle">关闭</view>
			</view>
			<view class="payline"></view>
			<view class="space"></view>
			<view v-for="(item,index) in payinfo" class="payinfo" @click="selectedpayway(index)">
				<image :src="item.thumb" class="payicin" mode="aspectFit"></image>
				<view class="paysubtitle">{{item.name}}</view>
				<image v-show="payselected == index" class="showyes" src=""></image>
				<view class="subpayline"></view>
			</view>
			
		</view>
	</view>
</template>
<script>
	import imtAudio from '@/components/imt-audio/imt-audio.vue';
	const app = getApp();
	export default {
		components: {
			imtAudio
		},
		data() {
			return {
				payselected:0,
				showpayview: false,
				live_info: {},
				name: '',
				adddtime: '',
				content: '',
				des: '',
				url: '',
				type: '',
				thumb: '',
				duration: '',
				currentTime: 0,
				videoContext: '',
				buttonimage: '',
				status: 1,
				start: '',
				end: '',
				trialtype: 0,
				showpayview: false,
				showjifen: false,
				payinfo: []
			}
		},
		onLoad(option) {
			if (option.trialtype) {
				this.trialtype = option.trialtype;
			} else {
				this.trialtype = 0;
			}
			this.thumb = option.thumb;
			//decodeURIComponent
			// #ifdef MP-WEIXIN
			this.live_info = JSON.parse(decodeURIComponent(option.info));
			// #endif
			// #ifdef H5
			this.live_info = JSON.parse(option.info);
			// #endif
			this.type = option.type; //JSON.parse(decodeURIComponent(option.info));
			this.name = this.live_info.name;
			this.content = this.live_info.content;
			this.des = this.live_info.des;
			this.adddtime = this.live_info.add_time;
			if (!this.adddtime) {
				this.adddtime = option.addtime;
			}
			if (this.type != 1) {
				this.url = this.decypt(this.live_info.url);
				if (this.type == 3) {
					this.videoContext = uni.createVideoContext('myVideo');
					this.buttonimage = '../../static/voice2.png';
				}
			}
		},
		methods: {
			fufei() {
				// #ifdef MP-WEIXIN
				this.buy('wx', 3);
				// #endif
				// #ifdef H5
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Cart.GetPayList',
					method: 'POST',
					data: {
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
					},
					success: res => {
						if (res.data.data.code == 0) {
							this.payinfo = res.data.data.info;
							this.showpayview = true;
						}
					}
				});
				// #endif
			},
			buy() {
				// #ifdef H5
				this.showpayview = false;
				uni.showToast({
					title: '支付未接入',
					icon:'none'
				});
				// #endif
				var that = this;
				var goodsArray = [];
				var newtiem = {};	
				newtiem['type'] = 0;
				newtiem['typeid'] = this.live_info.id;				
				goodsArray.push(newtiem);
				var json = JSON.stringify(goodsArray);
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Cart.Buy',
					method: 'POST',
					data: {
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
						'goods': json,
						'payid': 3,
						'addrid': 0,
						'openid': app.globalData.openid,
						'method': 0
					},
					success: res => {
						this.showpayview = false;
						if (res.data.data.code == 0){
						var ali = res.data.data.info[0].ali;
						var wx = res.data.data.info[0].wx;
						var h5 = res.data.data.info[0].h5;
						var small = res.data.data.info[0].small;
						var orderid = res.data.data.info[0].orderid;
						var money = res.data.data.info[0].money;
						// #ifdef MP-WEIXIN
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: String(small.timeStamp),
							nonceStr: small.nonceStr,
							package: small.package,
							signType: 'MD5',
							paySign: small.sign,
							success: function(res) {
								uni.showToast({
									title: '支付成功',
									icon: 'none'
								});
								uni.navigateBack({
									delta: 1
								});
							},
							fail: function(err) {
								uni.showToast({
									title: '支付失败',
									icon: 'none'
								});
							}
						});
						// #endif
							}else{
								uni.showToast({
									title: res.data.data.msg,
									icon:'none'
								});
							}
					}
				});
			},
			cancle() {
				this.showpayview = false;
			},
			selectedpayway(index) {
				this.payselected = index;
			},
			decypt(code) {
				var newcode = '';
				var str = '1ecxXyLRB.COdrAi:q09Z62ash-QGn8VFNIlb=fM/D74WjS_EUzYuw?HmTPvkJ3otK5gp&';
				for (var i = 0; i < code.length; i++) {
					var codeIteam = code[i];
					for (var j = 0; j < str.length; j++) {
						var stringIteam = str[j];
						if (codeIteam == stringIteam) {
							if (j == 0) {
								newcode += str[str.length - 1];
							} else {
								newcode += str[j - 1];
							}
						}
					}
				}
				return newcode;
			}
		}
	}
</script>
<style>
	.buyBG_voice {
		width: 100%;
		height: 600rpx;
		background-color: #696969;
	}

	.buyBG {
		/* position: absolute; */
		/* top: 0; */
		width: 100%;
		height: 400rpx;
		background-color: #696969;
	}

